// lib/siteConfig.ts

export type Lang = "en" | "ar";

export type LocalizedText = {
  en: string;
  ar: string;
};

export type MenuItem = {
  id: string;
  name: LocalizedText;
  description: LocalizedText;
  priceHalf?: number;
  priceFull: number;
  image?: string;
};

export type MenuCategoryId =
  | "main"
  | "desserts"
  | "drinks"
  | "breakfast"
  | "shisha";

export type MenuCategory = {
  id: MenuCategoryId;
  label: LocalizedText;
  items: MenuItem[];
};

export type OpeningRow = {
  label: LocalizedText;
  time: LocalizedText;
};

export type Offer = {
  id: string;
  title: LocalizedText;
  subtitle: LocalizedText;
  badge: LocalizedText;
  gradientFrom: string;
  gradientTo: string;
};

export type PopularItem = {
  id: string;
  name: LocalizedText;
  category: LocalizedText;
  price: number;
};

export interface SiteConfig {
  brandName: LocalizedText;
  primaryColor: string;

  hero: {
    badge: LocalizedText;
    titleLine1: LocalizedText;
    titleLine2: LocalizedText;
    description: LocalizedText;
    cta: LocalizedText;
    discountBadge: LocalizedText;
  };

  // (still named qrSection in code, but you can use it as Gallery)
  qrSection: {
    title: LocalizedText;
    text: LocalizedText;
    note: LocalizedText;
  };

  menuSection: {
    label: LocalizedText;
    title: LocalizedText;
    categories: MenuCategory[];
  };

  offersSection: {
    title: LocalizedText;
    offers: Offer[];
  };

  popularSection: {
    title: LocalizedText;
    items: PopularItem[];
  };

  footer: {
    about: LocalizedText;
    openingTitle: LocalizedText;
    openings: OpeningRow[];
    exploreTitle: LocalizedText;
    exploreLinks: LocalizedText[];
    contactTitle: LocalizedText;
    contacts: string[];
    newsletterTitle: LocalizedText;
    newsletterText: LocalizedText;
    newsletterPlaceholder: LocalizedText;
    newsletterButton: LocalizedText;
    socialLabel: LocalizedText;
    currencySymbol: string;
  };
}

